@import '../base';

.c-page--product {
  #gorgias-chat-container {
    display: none;

    @include t-media-query($s-medium-up) { 
      display: block;
    }
  }
}

.c-product-template {
  margin-top: 120px;

  &__container {
    padding-left: 20px;
    padding-right: 20px;
  }

  &__inner {
    position: relative; 
    z-index: 0;
    text-align: center;
    display: flex;
    flex-flow: column;
  }

  &__header-banner {
    position: relative;
  }

  &__banner-image {
    width: 100%;
    height: 100%;
    object-fit: cover;

    &.is-desktop {
      display: none;

      .has-no-mobile-image & {
        display: block;
      }
    }

    &-container {
      position: absolute;
      top: 0;
      width: 100%;
      left: 0;
      z-index: -1;
      height: 100%;
    }
  }

  &__product {
    &-info {
      padding: 2.3rem 0 0;
    }

    &-subtitle {
      text-align: center;
      text-transform: uppercase;
      color: $s-color--grey-green;
      font-size: 16px;
      line-height: 1.25;
      font-weight: 700;
      margin-bottom: t-to-rem(27);

      &.is-desktop {
        display: none;
      }
    }

    &-title {
      font-size: t-to-rem(50);
      text-align: center;
      color: $s-color--blue-green;
      margin: 0 auto t-to-rem(20);

      &.is-desktop {
        display: none;
      }
    }

    &-variant-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: t-to-rem(32);

      &.is-desktop {
        display: none;
      }

      .oke-sr-rating {
        display: inline;
        font-size: 22px;
        line-height: 1.2;
        font-weight: 500;
        top: -1px;
        position: relative;
      }
    
      .oke-sr-count-number {
        position: relative;
        font-size: 22px;
        line-height: 1.2;
        font-weight: 500;
        color: $s-color--grey-green;
        top: -1px;
        position: relative;
    
        &:before {
          content: "(";
          display: inline;
          position: relative;
        }
    
        &:after {
          content: ")";
          display: inline;
          position: relative;
        }
      }
    
      .oke-sr-label-text {
        display: none;
      }
    }

    &-variant {
      display: inline;
      font-size: 24px;
      font-weight: 500;
      color: $s-color--grey-green;
      margin-right: 40px;

      &:empty {
        margin-right: 0;
      }

      span + span {
        margin-left: 10px;
      }
    }

    &-about {
      font-size: 24px;
      line-height: 1.25;
      font-weight: 500;
      color: $s-color--grey-green;
      text-align: left;
      margin-bottom: 35px;
    }

    &-features {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(169px, 50%));
      grid-row-gap: 20px;
      margin-bottom: 30px;

      &-item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        color: #004851;
        font-size: 22px;
        font-weight: 500;
        line-height: 1.1;
        letter-spacing: 0px;
        text-align: left;
        padding: 0 3px 0 0;
      }

      &-item-icon {
        width: 20px;
        height: auto;
        margin-right: 10px;
        fill: #B4F1EA;
        top: 5px;
        position: relative;
      }
    }

    

    &-images {
      position: relative;
      margin-bottom: 10px;
    }

    &-image-container:not(.is-initialised) {
      display: flex;
      flex-direction: row;
      overflow: hidden;
      & > * {
        flex-shrink: 0;
        width: 100%;
      }
    }

    &-image-container__img-wrapper {
      width: 100%;

      &--bg {
        height: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100% 100%;
      }
    }

    &-navigation {
      display: flex;
      justify-content: space-between;
      position: absolute;
      width: 100%;
      bottom: -8px;
      left: 50%;
      transform: translateX(-50%);
      max-width: 24.625rem;
      padding: 0 16px;

      &-prev,
      &-next {
        content: "";
        background-position: center;
        background-repeat: no-repeat;
        width: 2rem;
        height: 2rem;
        background-size: 0.625rem 0.5625rem;
        border-radius: 50%;
        background-color: $s-color--background-tertiary;
        border: 2px solid white;
        z-index: 1;
        cursor: pointer;
      }

      &-next {
        right: 0;
        background-image: url(./../../assets/arrow-left-w.svg);
        transform: rotate(180deg);
      }

      &-prev {
        background-image: url(./../../assets/arrow-left-w.svg);
      }
    }

    &-dots {
      display: flex;
      width: 100%;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      gap: 8px;
      margin-top: 32px;
    }
    &-dot {
      display: flex;
      width: 16px;
      height: 16px;
      align-items: center;
      justify-content: center;
      padding: 4px;
      cursor: pointer;
      &-item {
        display: flex;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        border: 1px solid white;
      }
    }

    &-dot.is-active {
      padding: 2px;
    }

    &-image {
      height: 24.625rem;
      width: 100%;
      object-fit: contain;
      margin: 0 auto;

      &--thumb {
        height: auto;
        max-height: unset;
        object-fit: contain;
        max-width: 100% !important;
        width: auto;
      }
    }

    &-thumbnails {
      margin-top: t-to-rem(10);
      justify-content: center;

      .c-product-template__product-image-container__img-wrapper {
        width: 100%;
        height: 100%;
      }
    }

    &-thumbnail {
      border: 1px solid transparent;
      transition: border-color .3s;
      max-width: 89px !important;
      max-height: 89px !important;
      min-width: unset!important;

      &.is-active {
        border: 1px solid $s-color--mint-tulip;
      }
    }
  }

  &__product-details {
    background-color: $s-color--white;
    padding: 0 0 3rem;
  }

  &__accordion {
    // @include t-gutters;
    border-bottom: 1px solid rgba($s-color--background-secondary, .1);

    &:first-of-type {
      border-top: 1px solid rgba($s-color--background-secondary, .1);
    }

    &-wrapper {
      background-color: $s-color--white;
      text-align: left;

      &.is-desktop {
        display: none;
      }
    }

    &-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;
      padding: t-to-rem(24) 0;
      color: $s-color--font-1;

      .o-icon {
        pointer-events: none;
      }
    }

    &-content {
      @include t-font--type-8;
      max-height: 0;
      overflow: hidden;
      @include t-anim--transition(max-height margin, $s-anim--time-med);
      color: $s-color--font-1;

      p {
        @include t-font--type-8;
        color: $s-color--font-8;
      }

      ul {
        display: block;
        list-style-type: disc;
        margin-bottom: 1rem;
        margin-left: 0;
        margin-right: 0;
        padding-left: 1.25rem;
      }

      ol {
        list-style-type: decimal;
        list-style-position: inside;
        margin-bottom: 1rem;
        margin-left: 0;
        margin-right: 0;
        padding-left: 1.25rem;
      }

      li {
        color: $s-color--font-8;
      }

      p {
        &:not(:last-of-type) {
          margin-bottom: 1.25rem;
        }
      }

      .is-expanded & {
        max-height: 30rem;
        margin-bottom: t-to-rem(24);
        overflow: auto;
      }
    }

    .o-icon {
      @include t-width-and-height(12);

      &--minus {
        display: none;
      }
    }

    &.is-expanded {
      .o-icon {
        &--minus {
          display: block;
        }

        &--plus {
          display: none;
        }
      }
    }
  }

  &__afterpay {
    margin-bottom: 2.5rem;
    text-align: center;

    &-popup {
      .o-afterpay__afterpay {
        svg {
          height: 0.625rem;
          width: auto;
        }
      }

      .o-afterpay__klarna {
        svg {
          height: 0.5rem;
          width: auto;
        }
      }
    }

    span {
      display: block;
    }
  }

  &__panel {
    position: fixed;
    bottom: -250px;
    z-index: 4;
    background: $s-color--aqua;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    transition: bottom .4s ease-out;

    &.visible {
      bottom: 0;
    }

    &-swatches {
      display: none;
    }

    &-image {
      flex: 0 0 135px;
      max-width: 135px;
    }

    &-product-image {
      height: auto;
      max-height: 202px;
      width: auto;
    }

    &-info {
      display: flex;
      flex-grow: 1;
      flex-direction: column;
      align-items: flex-start;
      padding: 20px 0 10px 19px;
    }

    &-product-title {
      font-size: 40px;
      font-weight: 400;
      line-height: 1.1;
      text-transform: uppercase;
      margin-bottom: 20px;
      color: $s-color--white;
    }

    .c-product-template__product-features {
      display: none;
    }

    .o-quantity-selector__inner {
      width: 137px;
      margin-right: 40px;


      .o-quantity-selector__button {
        svg {
          width: 0.5rem;
          path {
            fill: $s-color--blue-green;
          }
        } 
      }
    }

    .c-product-details__add-cart.o-btn {
      margin: 25px 0 0 0;
      min-height: 55px;
      width: 100%;
      max-width: 238px;
      min-width: unset;
    }
  }

  @include t-media-query($s-small-up) {
    margin-top: 200px;

    &__banner-image {
      &.is-mobile {
        display: none;
      }

      &.is-desktop {
        display: block;
      }

      &-container {
        height: t-to-rem(440);
      }
    }

    &__title {
      margin-bottom: t-to-rem(20);
    }

    &__product-images {
      // width: 18.75rem;
      margin: 0 auto 30px;
      width: 100%;
      max-width: 750px;
    }

    &__product-image {
      height: auto;
      // max-height: 100%;
      width: 100%;
      max-width: 500px;
      max-height: 500px;
      margin: 0 auto;
      position: relative;

      &--thumb {
        height: auto;
      }

      &-container__img-wrapper {
        &--bg {
          // max-width: 500px;
          // max-height: 500px; 
          background-size: contain;
        }
      }
    }

    &__product-info {
      padding: 0;
    }

    &__product-features  {
      grid-template-columns: repeat(auto-fit, minmax(176px, 33%));
    }

    &__panel {
      padding-left: 3.5rem;
      padding-right: 3.5rem;
      justify-content: center;

      &-info {
        flex-grow: 0;
        padding-left: 0;
        margin-right: 25px;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    margin-top: 240px;

    &__title {
      margin-bottom: t-to-rem(24);
    }

    &__afterpay {
      margin-left: auto;
      margin-right: auto;
      width: 95%;

      span {
        display: initial;
      }
    }

    &__accordion {
      margin-right: 15px;

      &-wrapper {
        &.is-desktop {
          display: block;
        }
      }
    }

    &__inner {
      margin-bottom: t-to-rem(26);
      flex-flow: row;
    }

    &__header-banner {
      position: sticky;
      top: 0;
      // padding-top: 6rem;
      display: flex;
      align-items: center;
      justify-content: center;
      height: fit-content;
    }

    &__header-banner,
    &__product-details {
      width: 50%;
    }

    &__banner-image-container {
      left: auto;
      padding-bottom: 0;
      height: 100%;
    }

    &__product {
      &-info {
        padding: 0 0 2.6rem;
        width: 100%;
      }

      &-variant-wrapper,
      &-subtitle,
      &-title {
        text-align: left;
        &.is-desktop {
          display: block;
        }

        &.is-mobile {
          display: none;
        }
      }
      &-variant-wrapper {
        margin-bottom: t-to-rem(32);
      }
      &-about {
        margin-bottom: t-to-rem(40);
      }
      &-subtitle {
        font-size: t-to-rem(20);
        line-height: t-to-rem(24);
        margin-bottom: t-to-rem(22);
      }
      &-title {
        font-size: t-to-rem(60);
      }

      &-variant-wrapper {
        justify-content: flex-start;
        margin-top: t-to-rem(22);
        &.is-desktop {
          display: flex;
        }
      }

      &-details {
        padding-top: 0;
        margin: 0 auto;
        padding-left: 3rem;
        padding-right: 0;
        order: 0;
        text-align: left;
        align-self: flex-start;
      }

      &-images {
        width: 100%;
      }

      &-image {
        max-width: 600px;
      }

      &-image {
        // height: 100%;
        // max-height: 80vh;
        // position: absolute;
        // width: 100%;
        // object-fit: contain;
        // top: 50%;
        // transform: translateY(-50%);

        &-container {
          picture, &__img-wrapper {
            // display: block;
            // position: relative;
            // padding-bottom: 85%;
          }
        }
      }

      &-dots {
        margin-top: 42px;
      }

      &-thumbnails {
        margin-top: t-to-rem(30);
      }

      &-thumbnail {
        max-width: 100px !important;
        max-height: 100px !important;
        min-width: 100px!important;
        margin: 0 2.5px 0;
      }

      &-navigation {
        position: absolute;
        width: 85%;
        padding: 0;
        left: 50%;
        transform: translate(-50%, 0);
        z-index: 2;
        max-width: 80vh;
      }

      &-features  {
        grid-template-columns: repeat(auto-fit, minmax(176px, 50%));
      }
    }

    &__panel {
      flex-direction: row;
      padding: 20px 6.5rem 10px 3.5rem;
      justify-content: space-between;
      height: auto;
      min-height: 120px;
      height: 120px;

      &-info-inner {
        display: flex;
        flex-grow: 1;
        align-items: center;
      }

      &-info {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        flex-grow: 1;
        margin-right: 0;
        margin-left: 25px;
        padding: 0;
      }

      &-product-title {
        margin: 0;
        padding: 0 10px 0 0;
        flex-grow: 1;
      }

      &-image {
        transform: translate(0px, -25px);
      }

      &-product-image {
        max-height: 180px;
        margin: 0;
      }

      .c-product-template__product-image-container__img-wrapper {
        padding: 0;
        display: flex;
        justify-content: flex-end;
      }

      .c-product-details__add-cart.o-btn {
        margin: 0 0 0 40px;
        min-width: unset;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__product {
      &-features  {
        grid-template-columns: repeat(auto-fit, minmax(176px, 33%));
      }
    }

    &__panel {
      padding-left: t-to-rem(20);
      padding-right: 6.5%;

      &-product-title {
        padding: 0 15px 0 0;
        flex-grow: 0;
        // font-size: 60px;
        max-width: 40%;
        margin-bottom: 1px;
      } 

      &-info {
        margin-left: 15px;
      }

      .c-product-template__product-features {
        column-count: 3;
        display: block;
        margin: 0;
  
        &-item {
          font-size: 16px;
          font-weight: 500;
          color: $s-color--blue-green;
          margin-bottom: 7px;
        }

        &-item-icon {
          top: 0;
          width: 14px;
          margin-right: 7px;
        }
      }

      .o-quantity-selector__inner {
        margin-right: 20px;
        margin-left: 15px;
        max-width: 120px;
      }
    }
  }
  
  @include t-media-query($s-xlarge-up) {

    &__header-banner {
      width: 57%;
    }

    &__container {
      padding-left: 7%;
      padding-right: 7%;
    }

    &__banner-image-container {
      width: 100%;
    }

    &__product {
      &-details {
        // @include t-gutters(true);
        padding-top: 0;
        width: 50%;
        padding-bottom: 6rem;
        padding-left: t-to-rem(126);
        padding-right: 0 !important;
      }

      &-title {
        text-align: left;
        margin-bottom: 0;
        font-size: 109px;
      }
      &-variant-wrapper {
        margin-bottom: t-to-rem(44);
      }
      &-features {
        margin-bottom: 40px;
        grid-template-columns: repeat(auto-fit, minmax(176px, 33%));
      }

      &-image-container {
        picture, &__img-wrapper {
          // padding-bottom: 100%;
        }
      }

      &-image {
        max-width: 750px;
      }

      &-info {
        padding: 0 0 3.8rem 0;
      }

      &-images {
        max-width: calc(100vh - 18rem);
      }

      &-dots {
        margin-top: 64px;
      }

      &-navigation {
        padding: 0;
        bottom: -16px;
        max-width: unset;
        width: 100%;
        &-prev,
        &-next {
          width: 3.125rem;
          height: 3.125rem;
          background-size: 1rem 0.8125rem;
        }
      }
    }

    &__panel {
      padding-left: 6.5%;
      padding-right: 6.5%;

      &-product-title {
        padding: 0 40px 0 0;
        flex-grow: 0;
        font-size: 60px;
        margin-bottom: 0;
      } 

      .c-product-template__product-features {
        column-count: 3;
        display: block;
        margin: 0;
  
        &-item {
          font-size: 16px;
          font-weight: 500;
          color: $s-color--blue-green;
          margin-bottom: 7px;
        }

        &-item-icon {
          top: 0;
        }
      }

      .o-quantity-selector__inner {
        margin-right: 50px;
        margin-left: 25px;
        max-width: 180px;
      } 
    }
  }

  @include t-media-query($s-small-down) {
    &__product-image-container--fis {
      & > * {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      & > *:first-child {
        .c-product-template__product-image {
          height: 14.625rem;
        }
      }
    }
  }
}
